<template>
    <div class="container">
        <h1>Edit</h1>

        <form @submit.prevent="sendupdateUserRequest">

            <input type="text" class="form-control my-2" v-model="user.name">
            <input type="text" class="form-control my-2" v-model="user.email">
            <input type="password" class="form-control my-2" v-model="password">
            <b-button variant="warning" type="submit">Update</b-button>            

        </form>


    </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
    name: 'EditProfile',
    data(){
        return {
            password: 'anypassword'
        }
    },
    computed: {
        ...mapState(['token', 'user']),
    },
    methods: {
        sendupdateUserRequest() {
            let config = {
                headers: {
                    token: this.token
                }
            }
            this.user.password = this.password
            console.log(this.user)
            this.axios.put(`/users/${this.user._id}`, this.user, config)
                .then(response => {
                    console.log(response.data)
                    this.$router.push({name:'Profile'})
                })
                .catch(errors => console.log(errors.response.data))
        }
    }
}
</script>

